.about-me {
  padding: 60px 40px 0 40px;
  scroll-snap-align: start;
  min-height: 100vh;
  background-color: #000;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.about-me__text-box * {
  text-align: left;
}
.about-me__text-box .text-box__contact-box {
  display: flex;
  margin: 3px 0;
}
.about-me__text-box .text-box__contact-box .contact-box__icon {
  width: 25px;
  height: auto;
  margin-right: 5px;
}
.about-me__text-box .text-box__context {
  width: 40vw;
  margin-bottom: 10px;
}
.about-me__text-box .text-box__contact {
  width: 40vw;
}
.about-me__portrait {
  width: 50%;
  height: auto;
}
.about-me * {
  font-family: "Syne", sans-serif;
  color: #fff;
}

@media (max-width: 800px) {
  .about-me {
    padding: 60px 40px 50px 40px;
    flex-direction: column-reverse;
    justify-content: flex-start;
  }
  .about-me .text-box:nth-child(2) {
    margin-bottom: 70px;
  }
  .about-me .text-box__context {
    width: 80vw;
  }
  .about-me .text-box__contact {
    width: 80vw;
  }
  .about-me__portrait {
    width: 70%;
    height: auto;
    margin: 0 auto;
    margin-bottom: 20px;
  }
}
@media (max-width: 500px) {
  .about-me__portrait {
    width: 100%;
  }
}/*# sourceMappingURL=aboutMe.css.map */